import "./styles.css";
import CharacterData from "./CharacterData";
import { useState } from "react";

export default function App() {
  const [text, setText] = useState("");
  const [search, setSearch] = useState("");
  const [show, setShow] = useState(false);

  return (
    <div className="App">
      <h2>Search Breaking Bad Characters</h2>
      <form
        onSubmit={(e) => {
          e.preventDefault();
          setShow(false);
          setSearch(text);
        }}
      >
        <input
          type="text"
          value={text}
          onChange={(e) => setText(e.target.value)}
          placeholder="Character name"
        />
        <button
          onClick={(e) => {
            e.preventDefault();
            setShow(false);
            setSearch(text);
          }}
        >
          Search
        </button>
        <button
          onClick={(e) => {
            e.preventDefault();
            setShow(true);
            setSearch("");
            setText("");
          }}
          style={{ marginLeft: 10 }}
        >
          Show all
        </button>
      </form>
      {(show || search) && <CharacterData term={search} />}
    </div>
  );
}
